import Vue from 'vue';
import { State } from 'vuex-class';
import { Component, Prop, Model, Watch } from 'vue-property-decorator';
import { LockModel } from '@/ts/models';

import { Popup, CellGroup, Cell } from 'vant';
import Header from '@/components/common/header';

@Component({
    name: 'LockInfo',
    components: { Popup, CellGroup, Cell, Header }
})
export default class LockInfo extends Vue {
    @Model('close', { type: Boolean }) value!: boolean; // v-model
    @Prop() readonly lock!: LockModel;

    @State('units') units !: Array<string>;

    isShow: boolean = this.value; // 

    lockStatuses: any = {
        0: '',
        10: '',
        20: '',
        30: '',
        40: '',
        50: ''
    };
    lockStyles: any = {
        0: 'black',
        10: 'gray',
        20: 'green',
        30: 'red',
        40: 'pink',
        50: 'orange'
    };

    // 
    closePopup() {
        this.isShow = false;
        this.$emit('close', false);
    }

    @Watch('value')
    watchValue(value: boolean) {
        this.isShow = value;
    }
}
