import Vue from 'vue';
import { Component, Prop } from 'vue-property-decorator';
import { NavBar, Icon } from 'vant';

@Component({
    name: 'Header',
    components: { NavBar, Icon }
})
export default class Header extends Vue {
    @Prop() title!: string; // 
    @Prop({ type: Boolean, default: true }) isLeft!: boolean; // 
    @Prop({ type: Boolean, default: false }) isRight!: boolean; // 
    @Prop({ type: Boolean, default: true }) isBorder!: boolean; // 

    // 
    handleLeftClick() {
        this.$emit('left');
    }

    // 
    handleRightClick() {
        this.$emit('right');
    }
}
