import Vue from 'vue';
import { Component } from 'vue-property-decorator';

interface IRoute {
    path: string; // 
    icon: string; // 
    title: string; // 
}

@Component({
    name: 'Navs',
    components: {}
})
export default class Navs extends Vue {
    routes: Array<IRoute> = [
        { path: '/home/index', icon: 'home', title: '' },
        { path: '/asset/index', icon: 'asset', title: '' },
        { path: '/mine/index', icon: 'mine', title: '' }
    ];
    activePath: string = '';

    // router-link
    goPage(path: string) {
        let self = this;
        setTimeout(function() {
            self.$router.push(path);
        }, 300);
    }

    mounted() {
        let route = this.$route;
        this.activePath = route.path;
    }
}
