// NullUndefined
function isNullOrUndefined(obj: any) {
    return (
        Object.prototype.toString.call(obj) === '[object Null]' ||
        Object.prototype.toString.call(obj) === '[object Undefined]'
    );
}

// 
function isString(obj: any) {
    return Object.prototype.toString.call(obj) === '[object String]';
}

// 
function isNumber(obj: any) {
    return Object.prototype.toString.call(obj) === '[object Number]';
}

// 
function isDateString(str: string): boolean {
    return !isNaN(Date.parse(str));
}

// 
function isEmptyObject(obj: any): boolean {
    return !obj || JSON.stringify(obj) === '{}';
}

// yyyy/MM/dd hh:mm
function dateReplace(date: string): string {
    if (!date) {
        return '';
    }
    return date.replace(/-/g, '/');
}

// 
function dateConvert(date: string | number | Date): Date | null {
    if (!date) {
        return null;
    }

    let result: Date = new Date();
    if (typeof date === 'string') {
        if (date.indexOf('-') > -1) {
            result = new Date(dateReplace(date));
        } else {
            result = new Date(date);
        }
    } else if (typeof date === 'number') {
        result = new Date(date);
    } else if (date instanceof Date) {
        result = date;
    } else {
        result = new Date(date);
    }
    return result;
}

// 
function dateFormat(
    date: string | number | Date,
    format: string = 'yyyy-MM-dd hh:mm',
    isZeroize: boolean = true,
    isUTC: boolean = false
): string {
    const cdate = dateConvert(date);
    if (!cdate) {
        return '';
    }

    const dy = isUTC ? cdate.getUTCFullYear() : cdate.getFullYear(),
        dM = (isUTC ? cdate.getUTCMonth() : cdate.getMonth()) + 1,
        dd = isUTC ? cdate.getUTCDate() : cdate.getDate(),
        dh = isUTC ? cdate.getUTCHours() : cdate.getHours(),
        dm = isUTC ? cdate.getUTCMinutes() : cdate.getMinutes(),
        ds = isUTC ? cdate.getUTCSeconds() : cdate.getSeconds(),
        dS = isUTC ? cdate.getUTCMilliseconds() : cdate.getMilliseconds(),
        config: any = {
            'y+': dy.toString(),
            'M+': !isZeroize
                ? dM.toString()
                : dM < 10
                ? '0' + dM.toString()
                : dM.toString(),
            'd+': !isZeroize
                ? dd.toString()
                : dd < 10
                ? '0' + dd.toString()
                : dd.toString(),
            'h+': !isZeroize
                ? dh.toString()
                : dh < 10
                ? '0' + dh.toString()
                : dh.toString(),
            'm+': !isZeroize
                ? dm.toString()
                : dm < 10
                ? '0' + dm.toString()
                : dm.toString(),
            's+': !isZeroize
                ? ds.toString()
                : ds < 10
                ? '0' + ds.toString()
                : ds.toString(),
            S: dS.toString()
        };

    for (const key in config) {
        const pattern = new RegExp(key);
        if (!pattern.test(format)) {
            continue;
        }

        const matches = format.match(pattern);
        if (!matches) {
            continue;
        }

        const first = matches[0],
            value = config[key];
        if (key === 'S') {
            format = format.replace(first, value);
        } else {
            format = format.replace(
                first,
                value.substr(value.length - first.length)
            );
        }
    }
    return format;
}

// 
function dateMorning(date: string | number | Date): Date {
    const cdate: Date = dateConvert(date) || new Date(),
        year = cdate.getFullYear(),
        month = cdate.getMonth(),
        day = cdate.getDate();
    return new Date(year, month, day);
}

// 
function dateWeek(date: string | number | Date, type: string): string {
    const cdate: Date = dateConvert(date) || new Date();
    if (type === '') {
        return '' + ''.charAt(cdate.getDay());
    }
    return '' + ''.charAt(cdate.getDay());
}

// 
function dateLine(date: number | string): string {
    if (typeof date == 'number' && date <= 0) {
        return '--';
    }
    if (!date) {
        return '--';
    }

    const cdate = dateFormat(date),
        parts = cdate.split(' ');
    return parts.join('<br/>');
}

// 
function dateCalculate(
    date: Date,
    type: string = 'y',
    value: number = 0
): Date {
    let result: number = 0,
        clone = new Date(date.getTime());
    switch (type) {
        case 'y':
            result = clone.setFullYear(clone.getFullYear() + value);
            break;
        case 'M':
            result = clone.setMonth(clone.getMonth() + value);
            break;
        case 'd':
            result = clone.setDate(clone.getDate() + value);
            break;
        case 'h':
            result = clone.setHours(clone.getHours() + value);
            break;
        case 'm':
            result = clone.setMinutes(clone.getMinutes() + value);
            break;
        case 's':
            result = clone.setSeconds(clone.getSeconds() + value);
            break;
        case 'S':
            result = clone.setMilliseconds(clone.getMilliseconds() + value);
            break;
    }
    return new Date(result);
}

// 
function dateCountdown(seconds: number, isComplex: boolean = false): string {
    let secondTime = 0, // 
        minuteTime = 0, // 
        hourTime = 0, // 
        dayTime = 0; // 
    if (seconds > 60) {
        minuteTime = parseInt(String(seconds / 60)); // 60
        secondTime = parseInt(String(seconds % 60)); // 60

        if (minuteTime > 60) {
            hourTime = parseInt(String(minuteTime / 60)); // 60
            minuteTime = parseInt(String(minuteTime % 60)); // 60

            if (hourTime > 24) {
                dayTime = parseInt(String(hourTime / 24)); // 24
                hourTime = parseInt(String(hourTime % 24)); // 24
            }
        }
    } else {
        secondTime = parseInt(String(seconds));
    }
    if (!isComplex) {
        let result = '';
        if (seconds >= 3600 * 24) {
            result += `<i>${dayTime}</i>`;
        }
        if (seconds >= 3600) {
            result += `<i>${digitZeroize(hourTime)}</i>`;
        }
        if (seconds >= 60) {
            result += `<i>${digitZeroize(minuteTime)}</i>`;
        }
        if (seconds > 0) {
            result += `<i>${digitZeroize(secondTime)}</i>`;
        }
        return result;
    } else {
        let dayStr = '';
        if (seconds >= 3600 * 24) {
            dayStr = String(dayTime)
                .split('')
                .map((item: string) => `<i>${item}</i>`)
                .join('');
            dayStr += '<span></span>';
        }

        let hourStr = digitZeroize(hourTime)
            .split('')
            .map((item: string) => `<i>${item}</i>`)
            .join('');
        hourStr += '<span></span>';

        let minuteStr = digitZeroize(minuteTime)
            .split('')
            .map((item: string) => `<i>${item}</i>`)
            .join('');
        minuteStr += '<span></span>';

        let secondStr = digitZeroize(secondTime)
            .split('')
            .map((item: string) => `<i>${item}</i>`)
            .join('');
        secondStr += '<span></span>';
        return dayStr + hourStr + minuteStr + secondStr;
    }
}

// 
function dateDuration(seconds: number): string {
    let secondTime = 0, // 
        minuteTime = 0, // 
        hourTime = 0, // 
        dayTime = 0; // 
    if (seconds > 60) {
        minuteTime = parseInt(String(seconds / 60)); // 60
        secondTime = parseInt(String(seconds % 60)); // 60

        if (minuteTime > 60) {
            hourTime = parseInt(String(minuteTime / 60)); // 60
            minuteTime = parseInt(String(minuteTime % 60)); // 60

            if (hourTime > 24) {
                dayTime = parseInt(String(hourTime / 24)); // 24
                hourTime = parseInt(String(hourTime % 24)); // 24
            }
        }
    } else {
        secondTime = parseInt(String(seconds));
    }

    let result = '';
    if (seconds >= 3600 * 24) {
        result += `${dayTime}`;
    }
    if (seconds >= 3600) {
        result += `${hourTime}`;
    }
    if (seconds >= 60) {
        result += `${minuteTime}`;
    }
    // if (seconds > 0) {
    // 	result += `${secondTime}`;
    // }
    return result;
}

// true/false
function dateEqual(
    leftDate: Date,
    rightDate: Date,
    hasTime: boolean = false
): boolean {
    if (!hasTime) {
        return (
            dateMorning(leftDate).getTime() === dateMorning(rightDate).getTime()
        );
    }
    return leftDate.getTime() === rightDate.getTime();
}

// 
function arrayDistinct(arrs: any[], key: string = ''): any[] {
    if (arrs.length <= 0) {
        return [];
    }

    let temp: any = {},
        result: Array<any> = [];
    for (let i = 0; i < arrs.length; i++) {
        let item: any = arrs[i],
            value = key ? item[key] : JSON.stringify(item);
        if (!temp[value]) {
            result.push(item);
            temp[value] = true;
        }
    }
    return result;
}

// 
function arraySort(
    arrs: any[],
    field: string = '',
    isAsc: boolean = false
): any[] {
    if (arrs.length <= 0) {
        return [];
    }

    arrs.sort((m, n) => {
        const v1 = field ? m[field] : m,
            v2 = field ? n[field] : n;
        if (v1 > v2) {
            return isAsc ? 1 : -1;
        } else if (v1 < v2) {
            return isAsc ? -1 : 1;
        } else {
            return 0;
        }
    });
    return arrs;
}

// key
function getAllKeys(data: any): string[] {
    data = data || {};
    let keys: Array<any> = [];
    for (const key in data) {
        keys.push(key);
    }
    return keys;
}

// key
function getFirstKey(data: any): string {
    data = data || {};
    let firstKey = '';
    for (const key in data) {
        firstKey = key;
        break;
    }
    return firstKey;
}

// value
function getFirstValue(data: any): any {
    data = data || {};
    let firstValue = '';
    for (const key in data) {
        firstValue = data[key];
        break;
    }
    return firstValue;
}

// 
function buildParameters(parameters: { [key: string]: any }): string {
    if (!parameters || JSON.stringify(parameters) === '{}') return '';

    let temp: Array<any> = [];
    for (const key in parameters) {
        const value = parameters[key];
        temp.push(`${key}=${String(value)}`);
    }
    return temp.join('&');
}

// 
function resolveParameters(key: string): string {
    let regex = new RegExp('(^|&)' + key + '=([^&]*)(&|$)', 'i'),
        hash = window.location.hash || '',
        flag = hash.indexOf('?') + 1,
        matches = hash.substr(flag).match(regex);
    if (!matches) return '';
    return unescape(matches[2]);
}

// 
function digitZeroize(digit: number): string {
    return digit >= 0 && digit < 10 ? `0${String(digit)}` : String(digit);
}

// 
function digitPercent(
    digit: string | number,
    precision: number = 2
): string | number {
    if (isNullOrUndefined(digit)) return digit;

    let ndigit = Number(digit);
    if (isNaN(ndigit)) return digit;

    return (ndigit * 100).toFixed(precision);
}

// 
function digitPrecision(digit: string | number, precision: number = 2) {
    if (isNullOrUndefined(digit)) return digit;

    let ndigit = Number(digit);
    if (isNaN(ndigit)) return digit;

    return Number(ndigit.toFixed(precision));
}

// 
function repeatChar(ch: string, count: number) {
    let result = '';
    for (let i = 0; i < count; i++) {
        result += ch;
    }
    return result;
}

// json
function parseJSON<T extends object>(value: string = ''): T {
    try {
        return JSON.parse(value) as T;
    } catch (e) {
        throw e;
    }
}

// 
function duplicate<T extends object>(data: T): T {
    return parseJSON(JSON.stringify(data)) as T;
}

// 
function printError(err: any, flag: string, isAlert: boolean = false): void {
    window.console && window.console.log(`${flag} err:`, typeof err, err);
    isAlert && alert(`${flag} err:` + JSON.stringify(err, null, 2));
}

// 
function getRootUrl(): string {
    const { protocol, host, pathname } = window.location;
    return `${protocol}//${host}${pathname}`;
}

// 
function jumpTop() {
    const app = document.getElementById('app');
    app && app.scrollIntoView(true);
}

// IE9
function isIE9(): boolean {
    let appName = window.navigator.appName || '',
        appVersion = window.navigator.appVersion || '',
        version = appVersion.split(';')[1];
    if (!version) {
        return false;
    }

    version = version.replace(/[ ]/g, '').replace('MSIE', '');
    return appName == 'Microsoft Internet Explorer' && parseInt(version) <= 9;
}

// 
function isMobile(): boolean {
    const userAgent = window.navigator.userAgent || '';
    return /Android|webOS|iPhone|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
        userAgent
    );
}

// 
function isWechat(): boolean {
    const userAgent = window.navigator.userAgent || '';
    return /micromessenger/i.test(userAgent.toLowerCase());
}

// 
function isAndroid() {
    const userAgent = window.navigator.userAgent || '';
    return userAgent.indexOf('Android') > -1 || userAgent.indexOf('Linux') > -1;
}

// IOS
function isIOS() {
    const userAgent = window.navigator.userAgent || '';
    return !!userAgent.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
}

const Utils = {
    isNullOrUndefined, // NullUndefined
    isString, // 
    isNumber, // 
    isDateString, // 
    isEmptyObject, // 

    dateReplace, // yyyy/MM/dd hh:mm
    dateConvert, // 
    dateFormat, // yyyy/MM/dd hh:mm:ss
    dateMorning, // 0
    dateWeek, // 
    dateLine, // 
    dateCalculate, // 
    dateCountdown, // 
    dateDuration, // 
    dateEqual, // 

    arrayDistinct, // 
    arraySort, // 

    getAllKeys, // key
    getFirstKey, // key
    getFirstValue, // value

    buildParameters, // 
    resolveParameters, // 

    digitZeroize, // 
    digitPercent, // 
    digitPrecision, // 
    repeatChar, // 

    parseJSON, // json
    duplicate, // 
    printError, // 

    getRootUrl, // 
    jumpTop, // 
    isIE9, // IE9
    isMobile, // 
    isWechat, // 
    isAndroid, // 
    isIOS // IOS
};

export default Utils;
