import Vue from 'vue';
import { Component } from 'vue-property-decorator';
import {
    IAreaCode,
    HotAreaCodes,
    AreaCodes,
    defaultAreaCode
} from '@/ts/config';

import { Popup, Icon, Search, IndexAnchor, IndexBar, Cell } from 'vant';
import Header from '@/components/common/header';

@Component({
    name: 'AreaCode',
    components: { Popup, Icon, Search, IndexAnchor, IndexBar, Cell, Header }
})
export default class AreaCode extends Vue {
    areaCode: IAreaCode = defaultAreaCode; // 
    isShow: boolean = false; // 
    isSearch: boolean = false; // 

    hotAreaCodes: Array<IAreaCode> = HotAreaCodes; // 
    searchAreaCodes: Array<IAreaCode> = []; // 
    indexAreaCodes: any = {}; // 

    keyword: string = ''; // 

    // 
    openPopup() {
        this.isShow = true;
    }

    // 
    closePopup() {
        this.isShow = false;
    }

    // /
    getAreaCodesByKeyword(keyword: string): Array<IAreaCode> {
        return AreaCodes.filter(
            (areaCode: IAreaCode) =>
                (areaCode.name || '').indexOf(keyword) > -1 ||
                (areaCode.code || '').indexOf(keyword) > -1
        );
    }

    // input
    handleSearchInput() {
        let keyword = this.keyword;
        if (!keyword) {
            this.isSearch = false;
            this.searchAreaCodes = [];
        } else {
            this.isSearch = true;
            this.searchAreaCodes = this.getAreaCodesByKeyword(keyword);
        }
    }

    // clear
    handleSearchClear() {
        this.keyword = '';
        this.isSearch = false;
        this.searchAreaCodes = [];
    }

    // /
    chooseAreaCode(areaCode: IAreaCode) {
        this.isShow = false;
        this.areaCode = areaCode;
        this.$emit('change', areaCode);
    }

    // 
    initData() {
        let indexAreaCodes: any = {};
        'ABCDEFGHIJKLMNOPQRSTUVWXYZ'.split('').forEach((letter: string) => {
            indexAreaCodes[letter] = AreaCodes.filter(
                (areaCode: IAreaCode) =>
                    (areaCode.name || '')
                        .toLowerCase()
                        .indexOf(letter.toLowerCase()) === 0
            );
        });
        this.indexAreaCodes = indexAreaCodes;
    }

    created() {
        this.initData();
    }
}
